.image{
  height: 20vmax;
  margin-left: 40px;
  position: -webkit-sticky;
  position: sticky;
  /* order: 2; */
}
.image2{
  height: 20vmax;
  margin-left: 40px;
  position: -webkit-sticky;
  position: sticky;
  display: none;/* order: 2; */
}

.mobileContainer{
  order: -1;
}
.rec{
  -webkit-transform: rotate(12deg);
          transform: rotate(12deg);
    position: relative;
    /* top: -4%; */
    left:-40%;
    z-index: 1;
    size: 100;
    height: 6vmax;
}


@media all and (max-width:600px){

  .container{
    display: flex;
    flex-direction: column;
